const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.[\\/](?:stories\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(10);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/stories\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      './stories/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/\.\.\/packages\/core\/src(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.react\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(24);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/packages\/core\/src(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.react\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      '../../packages/core/src/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}